@keyframes fadeInOverlay {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInModal {
  from { transform: scale(0.9); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}

.project-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeInOverlay 0.3s ease-out forwards;
}

.project-modal {
  background: #fff;
  width: min(1000px, 90vw);
  max-height: min(800px, 80vh);
  border-radius: 8px;
  overflow: auto;   /* Allow the entire modal content to scroll */
  position: relative;
  display: flex;
  flex-direction: column;
  animation: fadeInModal 0.3s ease-out forwards;
}

.close-button {
  position: sticky;  /* So it remains visible at the top as you scroll */
  border-radius: 100%;
  top: 10px;
  right: 10px;
  width: 10px;
  background: transparent;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  z-index: 10;
  margin-left: 96%; /* ìž„ì‹œë°©íŽ¸ */
}

/* Modal content container */
.modal-content {
  display: flex;
  flex-direction: column;
  padding: 1rem;
}

/* Top row: Carousel and Info */
.modal-top {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
}

/* Left side: Carousel container */
.modal-carousel {
  flex: 1;
  /* Your Carousel component should handle its own sizing */
}

/* Right side: Project info */
.modal-info-top {
  flex: 1;
  display: flex;
  flex-direction: column;
  // justify-content: center;
  text-align: left;
}

.project-name {
  font-size: 2rem;
  margin-bottom: 0rem;
  margin-top: 0rem;
  color: #333;
}

.project-period {
  font-size: 1rem;
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
  color: #333;
}

.technologies {
  display: flex;
  // gap: 0.5rem;
  flex-direction: column;

  h1 {
    margin: 0;
  }
  .tech-container {
    display: flex;
    gap: 0.5rem;
    flex-direction: row;
    margin-bottom: 0.5rem;
    .tech-icon {
      width: 30px;
      height: 30px;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
  }
}

.project-description {
  color: #333;
  display: flex;
  flex-direction: column;
  margin-bottom: 0.5rem;
  p {
    margin: 0;
  }
  h1 {
    margin: 0;
  }
}

.project-link {
  display: flex;
  flex-direction: row;
  align-items: center;
  .github-link {
    font-size: 1rem;
    // margin-bottom: 0.5rem;
    color: #007bff;
    text-decoration: none;
    margin-right: 1rem;
    &:hover {
      text-decoration: underline;
    }
  
    img {
      height: 1rem;
    }
  }
  .download-button {
    // display: block;
    // margin: 1rem auto 0 auto;
    // background: #007bff;
    // color: #fff;
    // border: none;
    // border-radius: 4px;
    // padding: 0.5rem 1rem;
    // cursor: pointer;
    // font-size: 1rem;
    // transition: background 0.3s ease;
    // &:hover {
    //   background: #0056b3;
    // }
  }
}



/* Bottom section: Additional details */
.modal-bottom {
  border-top: 1px solid #e0e0e0;
  padding-top: 1rem;
  
  .description,
  .responsibilities,
  .architecture {
    margin-bottom: 1rem;
  }
  
  h2 {
    margin-bottom: 0.5rem;
    font-size: 1.5rem;
    color: #333;
  }
  
  .description p,
  .responsibilities p {
    font-size: 1rem;
    color: #555;
    line-height: 1.4;
  }
  
  .architecture img {
    max-width: 100%;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .modal-top {
    flex-direction: column;
  }
}
