.btn {
  position: relative;
  background-image: linear-gradient(135deg, #4c6af3 0%, #3c8bfa 100%);
//   padding: 14px 50px;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  border: none;
  color: #fff;
  cursor: pointer;
  outline: none;
  overflow: hidden;
  box-shadow: 0 14px 30px rgba(0, 0, 0, 0.15);
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 2px;

  span {
    position: relative;
  }
    
  &::before {
    --size: 0;
    content: '';
    position: absolute;
    left: var(--x);
    top: var(--y);
    width: var(--size);
    height: var(--size);
    background: radial-gradient(circle closest-side, #cbdcde, transparent);
    transform: translate(-50%, -50%);
    transition: all 0.2s ease, left 0s, top 0s;
  }
  
  &:hover::before {
    --size: 200px;
  }
}
